import { findAllByTestId } from '@testing-library/dom';
import React from 'react';
import { Component } from 'react';
import { Link } from 'react-router-dom';
import{Breadcrumb, BreadcrumbItem, Card,CardBody,CardImg,CardText,CardTitle, Container,Modal,ModalHeader,ModalBody,Button,Label,Row,Col} from 'reactstrap';
import {Control, LocalForm, Errors} from 'react-redux-form';
import {Loading} from './LoadingComponent';
import {baseUrl} from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

    function RenderDish({dish}){
        return(
            <div className="col-12 col-md-5 m-1">
                <FadeTransform in transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                    }}>
                    <Card>
                        <CardImg top src={baseUrl + dish.image} alt={dish.name}/>
                        <CardBody>
                            <CardTitle>{dish.name}</CardTitle>
                            <CardText>{dish.description}</CardText>
                        </CardBody>
                    </Card>
                </FadeTransform>
            </div>
        );
    }
    function RenderComments({comments,postComment, dishId}){
        return(
            <div className="col-12 col-md-5 m-1">
                <header><h4>Comments</h4></header>
                <ul className="list-unstyled">
                    <Stagger in>
                        {comments.map(comment => {
                            return(
                                <Fade in>
                                    <li key={comments.id}>
                                        {comment.comment}
                                        <br/>
                                        {`--${comment.author}, ${new Intl.DateTimeFormat('IST',{year:'numeric', month:'short', day:'2-digit'}).format   (new  Date(Date.parse(comment.date)))}`}
                                    </li>
                                </Fade>
                            )
                        })}
                    </Stagger>
                </ul>
                <CommentForm postComment={postComment} dishId={dishId} />
            </div>
        )
    }
    const DishDetail=(props)=> {
        if(props.isLoading) {
            return (
                <div className="container">
                    <div className="row">
                        <Loading />
                    </div>
                </div>
            );
        }
        else if(props.errMess) {
            return(
                <div className="container">
                    <div className="row">
                        <h4>{props.errMess}</h4>
                    </div>
                </div>
            )
        }
        else if(props.dish != null)
            return(
                <Container>
                    <div className="row">
                        <Breadcrumb>
                            <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                            <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                        </Breadcrumb>
                        <div className="col-12">
                            <h3> {props.dish.name} </h3>
                            <hr/>
                        </div>
                    </div>
                    <div className="row">
                        <RenderDish dish={props.dish}/>
                        <RenderComments comments={props.comments}
                            postComment={props.postComment}
                            dishId={props.dish.id}/>
                    </div>
                </Container>
            )
        else
            return(
                <div></div>
            )
    }
    const required = (val)=>val && val.length;
    const maxLength = (len) => (val) => !val || (val.length <= len);
    const minLength = (len) => (val) => val && (val.length >= len);

    class CommentForm extends Component{
        constructor(props){
            super(props);
    
            this.toggleCommentForm=this.toggleCommentForm.bind(this);
            this.handleSubmit=this.handleSubmit.bind(this);
            this.state={
                isControlFormOpen:false,
            };
        }
    
        toggleCommentForm(){
            this.setState({isControlFormOpen: !this.state.isControlFormOpen});
        }
    
        handleSubmit(values) {
            console.log('Current State is: ' + JSON.stringify(values));
            this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
        }
        render(){
            return(
                <>
                    <Button outline onClick={this.toggleCommentForm}><span className="fa fa-comments fa-lg"></span> Submit Comment</Button>
                    <Modal isOpen={this.state.isControlFormOpen} toggle={this.toggleCommentForm} >
                        <ModalHeader toggle={this.toggleCommentForm}>Submit Comment</ModalHeader>
                        <ModalBody className="col-10 align-self-center">
                            <LocalForm onSubmit={values=>this.handleSubmit(values)}>
                                <Row className="form-group">
                                    <Label htmlFor="rating">Rating</Label>
                                    <Col>
                                        <Control.select model=".rating" name="rating" id="rating" className="form-control offset-1">
                                            <option value="1">1</option>
                                            <option value="2">2</option>
                                            <option value="3">3</option>
                                            <option value="4">4</option>
                                            <option value="5">5</option>
                                        </Control.select>
                                    </Col>
                                </Row>
                                <Row className="form-group">
                                    <Label htmlFor="author">Your Name</Label>
                                    <Col>
                                        <Control.text model=".author" name="author" id="author" className="form-control" validators={{required, minLength:minLength(3), maxLength:maxLength(15)}} />
                                        <Errors
                                            className="text-danger"
                                            model=".author"
                                            show="touched"
                                            messages={{
                                                required : 'Required',
                                                minLength: 'Must be greater than 2 characters',
                                                maxLength: 'Must be 15 characters or less'
                                            }}
                                        />
                                    </Col>
                                </Row>
                                <Row className="form-group">
                                    <Label htmlFor="comment">Comment</Label>
                                    <Col>
                                        <Control.textarea model=".comment" name="comment" id="comment" className="form-control" placeholder="Review" rows="6"/>
                                    </Col>
                                </Row>
                                <Row>
                                    <Button type="submit" color="primary">Submit</Button>
                                </Row>
                            </LocalForm>
                        </ModalBody>
                    </Modal>
                </>
            )   
        }
    }
export default DishDetail;